.RegistrationForm {
    max-width: 400px;
    margin: auto;
    padding: 20px;
    background-color: #f4f4f4;
    border: 1px solid #ccc;
    border-radius: 5px;
}
  
.RegistrationForm h2 {
  text-align: center;
  margin-bottom: 20px;
}

.formGroup {
  margin-bottom: 20px;
}

.formGroup label {
  display: block;
  margin-bottom: 5px;
}

.formGroup input {
  width: 100%;
  padding: 10px;
  font-size: 16px;
  border-radius: 5px;
  border: 1px solid #ccc;
}

.buttonForm{
  width: 100%;
  padding: 10px;
  font-size: 16px;
  background-color: #4caf50;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.buttonGoogle{
  width: 100%;
  height: 10%;
  padding: 10px;
  font-size: 16px;
  background-color: #0077ff;
  color: #ffffff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}  
  